import { Component } from '@angular/core';
import { ModalController, IonicPage, NavController, NavParams } from 'ionic-angular';
import { ScreenOrientation } from '@ionic-native/screen-orientation';
import { AidePage } from '../aide/aide';
/**
 * Generated class for the ParametrePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-parametre',
  templateUrl: 'parametre.html',
})
export class ParametrePage {
  constructor(public navCtrl: NavController, public navParams: NavParams, public sc: ScreenOrientation, public modalCtrl: ModalController) {
    this.sc.lock(this.sc.ORIENTATIONS.PORTRAIT);
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ParametrePage');
  }

  openModalScrawl() {

    let modal = this.modalCtrl.create('ModalscrawlPage');
    modal.present();
  }

  openModalInfo() {

    let modal = this.modalCtrl.create('ModalinfoPage');
    modal.present();
  }

  aide() {
    this.navCtrl.push(AidePage);
  }

}



